body {
  margin: 0;
  padding: 0;
  font-family: "Rajdhani", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  min-width: 320px;
  color: #192B3F;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.center {
  width: 288px;
  margin: 0 auto;
  padding: 0 16px;
}
.center--slider {
  padding: 0;
  width: 320px;
}
@media (min-width: 720px) {
  .center {
    width: 688px;
  }
  .center--slider {
    width: 720px;
  }
}
@media (min-width: 1360px) {
  .center {
    width: 616px;
    padding: 56px 52px 0 92px;
  }
  .center--main {
    width: 1360px;
    display: flex;
    padding: 0;
  }
  .center--slider {
    width: 600px;
    padding: 0;
  }
}

.control-button {
  width: 20px;
  height: 20px;
  background-color: transparent;
  position: relative;
  border: none;
  cursor: pointer;
}
@media (min-width: 1360px) {
  .control-button {
    margin-left: -5px;
  }
}
.control-button::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 2px;
  background-color: #192B3F;
  top: 9px;
  left: 3px;
}
@media (min-width: 1360px) {
  .control-button::before {
    top: 11px;
    left: 7px;
  }
}
.control-button--increase::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 10px;
  background-color: #192B3F;
  top: 5px;
  left: 7px;
}
@media (min-width: 1360px) {
  .control-button--increase::after {
    top: 7px;
    left: 11px;
  }
}
@media (min-width: 1360px) {
  .control-button {
    width: 27px;
    height: 27px;
    border: 1px solid transparent;
  }
  .control-button:hover {
    border: 1px solid #E1E6ED;
    border-radius: 50%;
  }
}

.button {
  display: block;
  padding: 12px;
  font-size: 24px;
  line-height: 32px;
  color: #FFFFFF;
  background-color: #192B3F;
  border: none;
  font-family: inherit;
  cursor: pointer;
  width: 100%;
}
.button:hover {
  background-color: #2E4055;
}
.button:active {
  background-color: #3B536D;
}

.form {
  padding: 16px 0 0 0;
}
@media (min-width: 1360px) {
  .form {
    padding: 0;
  }
}

.form__top {
  color: #8A94A6;
  font-size: 18px;
  line-height: 28px;
  padding: 0 0 16px 0;
  border-bottom: 1px solid #E1E6ED;
}
@media (min-width: 720px) {
  .form__top {
    padding: 0 0 4px 0;
  }
}
@media (min-width: 1360px) {
  .form__top {
    padding: 0 0 8px 0;
  }
}

.form__heading {
  color: #192B3F;
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  margin: 16px 0;
}
@media (min-width: 720px) {
  .form__heading {
    font-size: 48px;
    line-height: 56px;
  }
}
@media (min-width: 1360px) {
  .form__heading {
    margin: 24px 0 16px;
  }
}

.form__description {
  margin: 0;
}
@media (min-width: 720px) {
  .form__description {
    font-size: 20px;
    line-height: 32px;
  }
}
@media (min-width: 1360px) {
  .form__description {
    max-width: 383px;
  }
}

.form__wrap {
  padding: 16px 0;
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #E1E6ED;
}
@media (min-width: 720px) {
  .form__wrap {
    column-gap: 72px;
    padding: 24px 0;
  }
}
@media (min-width: 1360px) {
  .form__wrap {
    padding: 16px 0;
    column-gap: 56px;
  }
}

.form__fieldset {
  border: none;
  margin: 0 0 24px 0;
  padding: 0;
}
.form__fieldset--size {
  margin: 0 0 32px 0;
}
@media (min-width: 720px) {
  .form__fieldset {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 36px 0;
  }
}

.form__input-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 8px 0 0 0;
}
@media (min-width: 1360px) {
  .form__input-list {
    padding: 4px 0 0 0;
  }
}
.form__input-list--size {
  margin: 0 0 26px -13px;
}
@media (min-width: 720px) {
  .form__input-list--size {
    margin: 0 0 0 -13px;
    padding: 0;
  }
}
@media (min-width: 1360px) {
  .form__input-list--size {
    margin: 0;
  }
}

.form__input-item:not(:last-child) {
  margin-right: 4px;
}

.form__input {
  display: none;
}
.form__input--quantity {
  display: block;
  font-size: 24px;
  line-height: 24px;
  color: #8A94A6;
  max-width: 30px;
  border: none;
  text-align: center;
  -moz-appearance: textfield;
}
.form__input--quantity::-webkit-outer-spin-button, .form__input--quantity::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
@media (min-width: 1360px) {
  .form__input--quantity {
    margin-right: 4px;
  }
}
.form__input--cost {
  display: block;
  font-size: 40px;
  line-height: 40px;
  color: #C9B8A8;
  max-width: 100px;
  padding-left: 20px;
  border: none;
  position: relative;
}

.form__label--color {
  position: relative;
  width: 48px;
  height: 48px;
  display: block;
}
@media (min-width: 1360px) {
  .form__label--color {
    width: 32px;
    height: 32px;
  }
}
.form__label--color::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #192B3F;
  top: 4px;
  left: 4px;
}
@media (min-width: 1360px) {
  .form__label--color::before {
    width: 26px;
    height: 26px;
    top: 3px;
    left: 3px;
  }
}
.form__label--color::after {
  position: absolute;
  content: "";
  width: 46px;
  height: 46px;
  border-radius: 50%;
  border: 1px solid transparent;
  top: 0;
  left: 0;
}
@media (min-width: 1360px) {
  .form__label--color::after {
    width: 30px;
    height: 30px;
  }
}
.form__label--color:hover::after {
  border-color: #8A94A6;
}
.form__label--grey::before {
  background-color: #8A94A6;
}
.form__label--blue::before {
  background-color: #3D5876;
}
.form__label--size {
  text-transform: uppercase;
  font-size: 20px;
  line-height: 24px;
  color: #8A94A6;
  border: 1px solid transparent;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form__label--size:hover {
  border-color: #8A94A6;
}
@media (min-width: 1360px) {
  .form__label--size {
    width: 32px;
    height: 32px;
    font-size: 16px;
    line-height: 24px;
  }
}

.form__input:checked + .form__label--color::after {
  border-color: #192B3F;
}
.form__input:checked + .form__label--size {
  font-size: 28px;
  color: #192B3F;
  border: 1px solid #192B3F;
}
@media (min-width: 1360px) {
  .form__input:checked + .form__label {
    font-size: 20px;
  }
}

.form__input:disabled + .form__label--size {
  opacity: 0.5;
  color: #E1E6ED;
  border-color: transparent;
}

.form__size-guide {
  text-decoration: none;
  color: #C9B8A8;
  border: 1px solid #C9B8A8;
  border-radius: 24px;
  padding: 2px 12px;
}
@media (min-width: 720px) {
  .form__size-guide {
    align-self: center;
    margin: 0 0 0 25px;
  }
}

.form__container {
  margin-right: 74px;
}
@media (min-width: 720px) {
  .form__container {
    margin-right: 68px;
  }
}
@media (min-width: 1360px) {
  .form__container {
    margin-right: 12px;
  }
}

.form__input-wrap {
  display: flex;
  gap: 5px;
  align-items: center;
  margin: 14px 0 0 0;
}
@media (min-width: 1360px) {
  .form__input-wrap {
    margin: 10px 0 0 0;
  }
}

.form__cost-integer {
  font-size: 40px;
  line-height: 40px;
  color: #C9B8A8;
  margin: 10px 0 0 0;
  display: inline-block;
}
@media (min-width: 1360px) {
  .form__cost-integer {
    margin: 6px 0 0 0;
  }
}

.form__cost-float {
  font-size: 24px;
  line-height: 24px;
  color: #8A94A6;
  margin: 10px 0 0 0;
  display: inline-block;
}

.form__bottom {
  padding: 28px 0 40px 0;
}
@media (min-width: 1360px) {
  .form__bottom {
    padding: 24px 0 72px 0;
  }
}

.form__details {
  padding: 0;
  margin: 8px 0;
}

.form__note {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
  color: #8A94A6;
}

.main {
  padding: 0 0 16px 0;
}
@media (min-width: 720px) {
  .main {
    padding: 0 0 29px 0;
  }
}
@media (min-width: 1360px) {
  .main {
    padding: 0;
  }
}

.main-logo--slider {
  grid-row: 1/2;
  grid-column: 1/-1;
  margin: 24px 16px;
  z-index: 2;
}
@media (min-width: 1360px) {
  .main-logo--slider {
    margin: 32px 52px;
  }
}

@media (min-width: 1360px) {
  .main-logo__img {
    width: 95px;
    height: 53px;
  }
}

.section-heading {
  font-size: 24px;
  line-height: 32px;
  font-family: inherit;
  font-weight: inherit;
  color: inherit;
  margin: 0;
}
@media (min-width: 720px) {
  .section-heading--subscribe {
    font-size: 32px;
    line-height: 40px;
  }
}

.service__list {
  list-style: none;
  margin: 0;
  padding: 40px 0 0 0;
}
@media (min-width: 1360px) {
  .service__list {
    padding: 16px 0 0 0;
  }
}

.service__item {
  padding: 0;
  margin: 0;
  border-bottom: 1px solid #E1E6ED;
  position: relative;
}
.service__item:last-child {
  margin: 0;
}

.service__button {
  width: 100%;
  padding: 16px 0 10px;
  margin: 0;
  text-align: left;
  display: block;
  background: none;
  border: none;
  position: relative;
  cursor: pointer;
}
.service__button::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 2px;
  background-color: #192B3F;
  top: 30px;
  right: 3px;
}
.service__button--increase::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 10px;
  background-color: #192B3F;
  top: 26px;
  right: 7px;
}

.service__description {
  color: #8A94A6;
  display: none;
  padding: 0 0 10px 0;
  margin: 0;
}
.service__description--show {
  display: block;
}

.service__note {
  display: block;
  margin: 10px 0 0 0;
}

.similar {
  padding: 40px 0 0 0;
}

.similar__list {
  list-style: none;
  margin: 8px 0 0 0;
  padding: 0;
}
@media (min-width: 1360px) {
  .similar__list {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }
}

.similar__item {
  margin: 0 0 16px 0;
  border-radius: 5px;
  overflow: hidden;
}
.similar__item:last-child {
  margin: 0;
}
@media (min-width: 720px) {
  .similar__item {
    margin: 0 0 28px 0;
  }
}
@media (min-width: 1360px) {
  .similar__item {
    margin: 0;
  }
}

.similar__link {
  position: relative;
  display: block;
}

.similar__text {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  padding: 108px 0 12px 16px;
  background: linear-gradient(to top, rgba(53, 41, 37, 0.22), transparent);
}
@media (min-width: 720px) {
  .similar__text {
    padding: 108px 0 18px 16px;
  }
}
@media (min-width: 1360px) {
  .similar__text {
    padding: 108px 0 12px 16px;
  }
}

.similar__heading {
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #FFFFFF;
  display: block;
}
@media (min-width: 720px) {
  .similar__heading {
    font-size: 40px;
    line-height: 40px;
  }
}
@media (min-width: 1360px) {
  .similar__heading {
    font-size: 24px;
    line-height: 24px;
  }
}

.similar__slogan {
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;
  opacity: 0.5;
  display: block;
}
@media (min-width: 720px) {
  .similar__slogan {
    font-size: 18px;
    line-height: 18px;
  }
}
@media (min-width: 1360px) {
  .similar__slogan {
    font-size: 14px;
    line-height: 16px;
  }
}

.similar__img {
  display: block;
}
@media (min-width: 720px) {
  .similar__img {
    width: 688px;
    height: 287px;
  }
}
@media (min-width: 1360px) {
  .similar__img {
    width: 296px;
    height: 160px;
  }
}

.slider {
  display: grid;
  grid-template-rows: min-content 1fr;
  position: relative;
}
@media (min-width: 1360px) {
  .slider {
    position: sticky;
    top: 0;
  }
}

.slider__list {
  list-style: none;
  margin: 0;
  padding: 0;
  grid-row: 1/-1;
  grid-column: 1/-1;
  display: grid;
}

.slider__item {
  grid-column: 1/-1;
  grid-row: 1/-1;
  opacity: 0;
  transition: opacity 1.5s;
  position: relative;
}
.slider__item--current {
  opacity: 1;
}

.slider__text {
  padding: 0 16px 28px;
  color: #FFFFFF;
  background: linear-gradient(to top, rgba(53, 41, 37, 0.5), transparent);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
@media (min-width: 1360px) {
  .slider__text {
    padding: 0 0 32px 52px;
  }
}

.slider__item-name {
  font-size: 48px;
  line-height: 48px;
  font-weight: normal;
  margin: 0;
}
@media (min-width: 720px) {
  .slider__item-name {
    font-size: 64px;
    line-height: 64px;
  }
}
@media (min-width: 1360px) {
  .slider__item-name {
    font-size: 72px;
    line-height: 72px;
  }
}

.slider__slogan {
  font-size: 16px;
  line-height: 24px;
  font-weight: normal;
  opacity: 0.8;
  margin: 0 0 32px 0;
}
@media (min-width: 720px) {
  .slider__slogan {
    font-size: 24px;
    line-height: 32px;
    font-weight: normal;
  }
}
@media (min-width: 1360px) {
  .slider__slogan {
    font-size: 28px;
  }
}

.slider__counter {
  margin: 0 0 0 160px;
  font-size: 20px;
  line-height: 32px;
}

.slider__counter-total {
  opacity: 0.5;
}

.slider__img {
  grid-row: 1/-1;
  grid-column: 1/-1;
  z-index: -1;
  display: block;
}
@media (min-width: 720px) {
  .slider__img {
    width: 720px;
  }
}
@media (min-width: 1360px) {
  .slider__img {
    width: 600px;
    height: 768px;
  }
}

.slider__buttons {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 10px;
  position: absolute;
  left: 11px;
  bottom: 25px;
}
@media (min-width: 1360px) {
  .slider__buttons {
    left: 42px;
    bottom: 30px;
  }
}

.slider__button {
  width: 40px;
  height: 40px;
  position: relative;
  background: transparent;
  border: 1px solid transparent;
}
.slider__button:hover {
  border-radius: 50%;
  border: 1px solid #FFFFFF;
}
.slider__button:active {
  background: #FFFFFF;
}
.slider__button--prev::before {
  position: absolute;
  content: "";
  width: 12px;
  height: 3px;
  background: #FFFFFF;
  border-radius: 3px;
  transform: rotate(-45deg);
  top: 14px;
  left: 12px;
}
.slider__button--prev::after {
  position: absolute;
  content: "";
  width: 12px;
  height: 3px;
  background: #FFFFFF;
  border-radius: 3px;
  transform: rotate(45deg);
  top: 21px;
  left: 12px;
}
.slider__button--prev:active::before, .slider__button--prev:active::after {
  background: #8A94A6;
}
.slider__button--next::before {
  position: absolute;
  content: "";
  width: 12px;
  height: 3px;
  background: #FFFFFF;
  border-radius: 3px;
  transform: rotate(45deg);
  top: 14px;
  left: 13px;
}
.slider__button--next::after {
  position: absolute;
  content: "";
  width: 12px;
  height: 3px;
  background: #FFFFFF;
  border-radius: 3px;
  transform: rotate(-45deg);
  top: 21px;
  left: 13px;
}
.slider__button--next:active::before, .slider__button--next:active::after {
  background: #8A94A6;
}

.subscribe {
  padding: 52px 0 0 0;
}
@media (min-width: 720px) {
  .subscribe {
    padding: 48px 0 0 0;
  }
}
@media (min-width: 1360px) {
  .subscribe {
    padding: 64px 0 0 0;
  }
}

.subscribe__text {
  font-size: 18px;
  line-height: 28px;
  color: #8A94A6;
  margin: 8px 0 28px 0;
}
@media (min-width: 720px) {
  .subscribe__text {
    font-size: 20px;
    line-height: 32px;
    margin: 8px 0 24px 0;
  }
}

.subscribe__fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.subscribe__fieldset-gender {
  margin: 12px 0 16px 0;
}
@media (min-width: 720px) {
  .subscribe__fieldset-gender {
    display: flex;
    align-items: center;
    gap: 24px;
    margin: 12px 0 40px 0;
  }
}

.subscribe__legend {
  margin: 0 0 5px 0;
  font-size: 20px;
  line-height: 32px;
  color: #8A94A6;
}
@media (min-width: 720px) {
  .subscribe__legend {
    margin: 0;
  }
}

.subscribe__input-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.subscribe__input-list--flex {
  display: flex;
  flex-wrap: wrap;
  gap: 18px;
}

.subscribe__input-item {
  margin: 0 0 16px 0;
}
.subscribe__input-item:last-child {
  margin: 0;
}

@media (min-width: 720px) {
  .subscribe__fieldset-gender .subscribe__input-item {
    margin: 0;
  }
}

.subscribe__input {
  padding: 16px;
  background: rgba(225, 230, 237, 0.2);
  border-radius: 1px;
  border: none;
  width: 100%;
  box-sizing: border-box;
}
.subscribe__input::placeholder {
  font-size: 16px;
  line-height: 40px;
  color: #8A94A6;
}
@media (min-width: 720px) {
  .subscribe__input {
    max-width: 456px;
  }
}

.subscribe__radio {
  display: none;
}

.subscribe__label {
  margin: 0 0 0 31px;
  position: relative;
}
.subscribe__label::before {
  position: absolute;
  content: "";
  width: 19px;
  height: 19px;
  border: 1px solid #E1E6ED;
  border-radius: 50%;
  top: 0;
  left: -30px;
}

.subscribe__radio:checked + .subscribe__label::after {
  position: absolute;
  content: "";
  width: 13px;
  height: 13px;
  background: #192B3F;
  border-radius: 50%;
  top: 4px;
  left: -26px;
}

.subscribe__checkbox {
  display: none;
}

.subscribe__checkbox-label {
  margin: 0 0 16px 31px;
  position: relative;
  display: block;
}
.subscribe__checkbox-label::before {
  position: absolute;
  content: "";
  width: 19px;
  height: 19px;
  border: 1px solid #E1E6ED;
  border-radius: 1px;
  top: 0;
  left: -30px;
}
.subscribe__checkbox-label:hover::before {
  border: 1px solid #192B3F;
}
.subscribe__checkbox-label:hover::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 5px;
  border-left: 1px solid #E1E6ED;
  border-bottom: 1px solid #E1E6ED;
  transform: rotate(-45deg);
  top: 5px;
  left: -25px;
}

.subscribe__checkbox:checked + .subscribe__checkbox-label::before {
  background: #192B3F;
  border-color: #192B3F;
}
.subscribe__checkbox:checked + .subscribe__checkbox-label::after {
  position: absolute;
  content: "";
